@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crafty+Girls&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url('https://raw.githubusercontent.com/LizzieCrockett/react-milestone-ii/master/src/images/desktop.png');
}

.wrapper {
  max-width: 500px;
  margin: 0 auto;
  background-color: aliceblue;
  }


.main-content {
  margin: 0 20px;
  }



  @media screen and (max-width: 1000px) {
    body {
      background-image: url('https://raw.githubusercontent.com/LizzieCrockett/react-milestone-ii/master/src/images/handheld.png');
    }
  }
.welcome-message {
    text-align: center;
    font-family: 'Architects Daughter', cursive;
}
.nav {
    margin: 20px;
    padding: 10px;
    border-bottom: 2px solid palegreen;
    }
  
.nav ul {
    padding: 0;
    margin: 0;
    }
  
.nav button {
    font-size: 24px;
    font-family: 'Fredericka the Great', cursive;
    background: transparent;
    border: none;
    cursor: pointer;
    }
  
.nav-item {
    list-style: none;
    display: inline-block;
    margin-right: 32px;
    font-size: 24px;
    border-bottom: 4px solid transparent;
    }
  
.nav-item button:hover {
      color: #999;
      }
  
.nav-item.selected {
      background-color: pink;
      border: none;
      }
  
.shop-page-items {
    margin-right: 10px;
    }
    
.shop-page-items li {
    list-style: none;
    margin-bottom: 20px;
    }
.item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid palegreen;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace
    }
.item-image {
    width: 64px;
    height: 64px;
    border: 1px solid #ccc;
    margin-right: 10px;
    float: left;
    }
.item-name {
    font-weight: bold;
    margin-bottom: 0.4em;
    }
.item-price {
    text-align: right;
    font-size: 18px;
    color: #555;
    margin-bottom: 10px;
    }

.item-left {
    flex: 1 1;
}
.item-right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.empty-cart-message {
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
}
.cart-page-items {
    margin: 0;
    padding: 0;
  }

.cart-page-items li {
    list-style: none;
  }

.cart-item-count {
    padding: 5px 10px;
    border: 1px solid #ccc;
  }

.cart-item-minusone, .cart-item-plusone {
    padding: 5px 10px;
    border: 1px solid #ccc;
    background: #fff;
  }

.cart-item-minusone {
    border-right: none;
  }

.cart-item-plusone {
    border-left: none;
  }
  
.cart-page-total {
  text-align: right;
  font-size: 25px;
  padding-right: 10px;
  margin-top: 20px;
}

button {
  font-family: 'Crafty Girls', cursive;
  background-color: peachpuff;
  border: none;
}
